import React, { useState } from "react";
import "./css/App.css";
import axios from "axios";
import SearchBar from "./components/SearchBar";
import WeatherCard from "./components/WeatherCard";
import Favorites from "./components/Favorites";
import CityFilter from  "./components/CityFilter";
import ForecastTiles from  "./components/ForecastTiles";
import {API_KEY} from "./config";

const App = () => {
  const [weather, setWeather] = useState(false);
  const [forecast, setForecast] = useState(false);
  

  const [searchDone, apidone] = useState(null);
    // Fetches weather from a search
    const fetchWeather = async (city) => {
      try {
       
        const API_KEY = '605068c077df37dcf4cc81dd1e4698b4'
        const url = `https://api.openweathermap.org/data/2.5/weather?q=${city}&APPID=${API_KEY}`;
        const { data } = await axios.get(url
        );
        console.log(data);
        const weatherObj = {
          weather: data.weather,
          city: data.name,
          country: data.sys.country,
          temp: data.main.temp
        };
        setWeather(weatherObj)
        apidone(true);
        fetchWeatherforecast(city)
      } catch (e) {
        console.log(e);
      }
      // fetchWeatherforecast(city)
    };


    const fetchWeatherforecast = async (city) => {
      try {
       
        const API_KEY = '605068c077df37dcf4cc81dd1e4698b4'
        const url = `https://api.openweathermap.org/data/2.5/forecast?q=${city}&APPID=${API_KEY}`;
        const { data } = await axios.get(url
        );
        console.log(data);
        // const weatherObj = {
        //   weather: data.weather,
        //   city: data.name,
        //   country: data.sys.country,
        //   temp: data.main.temp
        // };
        setForecast(data);
      } catch (e) {
        console.log(e);
      }
    };
    const { city, list } = forecast;
    return (
      
      <div className="App">
        <CityFilter fetchWeather={fetchWeather} 
         />
        <SearchBar
          fetchWeather={fetchWeather} 
          // error={errorMessage}
        />
        {searchDone && (
          <WeatherCard
            weatherData={weather}
            // weatherData={weatherData}
            savedCities={[]}
          />
        )}
            {list && (<ForecastTiles forecasts={list} />)}
            
      </div>
    );
}
export default App;




