import React, { useState } from "react";

const countryCodes = [
{Code:1, Name:'Agra'},
{Code:2, Name:'Delhi'},
{Code:3, Name:'Noida'}
]

const CityFilter = ({fetchWeather }) => {
  const [searchQuery, setSearchQuery] = useState("");

  const onSearch = e => {
    fetchWeather(searchQuery);
    setSearchQuery("");
  };
  return (
    <div>
          Search City : {searchQuery}
          <select className="form-control selcls form-control-sm" onChange={e => {
              setSearchQuery(e.target.value);
              console.log(e.target.value);
            }}>
            <option value="" />
            {countryCodes.map(country => (
              <option key={country.Code} value={country.Name}>
                {country.Name}
              </option>
            ))}
          </select>
          <button  variant="primary"class="btn btn-primary"  onClick={e => onSearch(e)}>Search</button>

    </div>
  );
};

export default CityFilter;



